<template>
    <div class="builder">
        <div class="builder__wrapper">
            <div class="builder-modules builder__modules">
                <div class="builder-modules__title">
                    <span>Modules</span>
                </div>
                <draggable element="div" :list="components" class="collection modules-list" :options="componentsOptions">
                    <div v-for="component in components" class="modules-list__item">
                        <img :src="component.thumb" :alt="component.label" class="modules-list__thumb">
                    </div>
                </draggable>
            </div>
            <div class="builder__content">
                <div class="builder-pane builder__pane">
                    <div class="builder-pane__content">
                        <draggable element="div" :list="formItems" :class="['collapsible pane', !formItems.length ? 'empty' : '']" :options="formOptions" :move="onMove" @start="isDragging=true" @end="isDragging=false" data-content="Drag and drop your modules here">
                            <div v-for="(item, index) in formItems" :key="index">
                                <div class="builder-module builder-pane__module">
                                    <div class="right">
                                        <div @click.stop="destroy(index)">Delete</div>
                                    </div>
                                    <div v-html="item.template"></div>
                                </div>
                            </div>
                        </draggable>
                    </div>
                </div>
                <div class="builder__debug">
                    <div v-if="formItems.length > 0">
                        <a class="btn right waves-effect red" @click.stop="clearItems()">Clear</a>
                    </div>
                    <input type="hidden" name="inputs" :value="listString">
                    <pre>{{listString}}</pre>
                </div>
            </div>
        </div>
    </div>
</template>

