tr> <td height="25"></td> </tr> <!-- btn --> <tr> <td align="center"> <table data-bgcolor="Button" bgcolor="#F36C5E" style="border-radius: 50px;" align="center" border="0" cellpadding="0" cellspacing="0"> <tbody><tr> <td data-link-style="text-decoration:none; color:#FFFFFF;" data-link-color="Button Link" data-size="Button" mc:edit="farbe-22" class="fallback-font" height="50" align="center" style="font-family:Montserrat, Arial, sans-serif; font-size: 17px;color: #FFFFFF; padding-left: 25px; padding-right: 25px;"><a href="#" style="text-decoration:none; color:#FFFFFF" data-color="Button Link">READ MORE</a></td> </tr> </tbody></table> </td> </tr> <!-- end btn --> <tr> <td height="50"></td> </tr> </tbody></table> </td> </tr> </tbody></table> </td> </tr> </tbody></table>',
