                        thumb: 'http://www.stampready.net/dashboard/editor/user_uploads/zip_uploads/2017/04/08/gwaryc49XNB3zAoHOE8VUPJD/StampReady-demo/thumbnails/1-2-left-feature.jpg'
                    }
                ],

                formItems:[],
                isDragging: false,
                delayedDragging:false
            }

        },

        methods: {
            onMove ({relatedContext, draggedContext}) {
                const relatedElement = relatedContext.element;
                const draggedElement = draggedContext.element;
                return (!relatedElement || !relatedElement.fixed) && !draggedElement.fixed
            },

            onClone(el) {
                var input = JSON.parse(JSON.stringify(el));
                input.name = input.id + '-' + Date.now();
                input.fixed = false;
                input.required = false;
                input.description = '';

                return input
            },

            destroy (index) {
                this.formItems.splice(index, 1)
            },

            clearItems () {
                this.formItems =[];
            },

        },
        computed: {

            componentsOptions () {
                return  {
                    sort: false,
                    group:{
                        name:'components',
                        pull:'clone',
                        put:false
                    }
                }
            },

            formOptions () {
                return  {
                    handle: '.builder-module',
                    group:{
                        name:'components'
                    }
                }
            },

            listString(){
                return JSON.stringify(this.formItems);
            }

        },

        watch: {

            isDragging (newValue) {
                if (newValue){
                    this.delayedDragging= true
                    return
                }
                this.$nextTick( () =>{
                    this.delayedDragging =false
                })
            }

        }
    }
</script>

<style lang="scss">

    .builder {

        &__wrapper {
            width: 100%;
            display: flex;
            min-height: 100%;
            align-items: stretch;
        }

        &__modules {
            position: fixed;
            flex-basis: 30rem;
            flex-shrink: 0;
            width: 30rem;
            height: calc(100vh - 10.9rem);
        }

        &__content {
            flex: 1;
            margin-left: 34rem;
        }

        &__pane {
            display: flex;
            justify-content: center;
        }

        &__debug {
            max-width: 50rem;
            overflow-x: scroll;
            background: rgba(#d3dce6, .5);
            padding: 1rem;
        }
    }

    .builder-pane {
        &__content {
            width: 100%;
            max-width: 60rem;
        }
    }

    .builder-modules {
        display: flex;
        flex-direction: column;
        border-radius: .4rem;
        background: #d3dce6;

        &__title {
            padding: 2rem;
            font-size: 1.6rem;
            font-weight: 500;
        }
    }

    .pane {
        position: relative;
        margin: 0;
        padding: 0;
        min-height: 30rem;

        &.empty {
            border-radius: .4rem;
            border-color: #d3dce6;
            border-width: 1px;
            border-style: dashed;
            box-shadow: 0 1px 1px 0 rgba(31,45,61,.05);

            &:after {
                position: absolute;
                content: attr(data-content);
                top: 50%;
                left: 50%;
                transform: translate(-50%, -50%);
                text-align: center;
                font-weight: 500;
            }
        }
    }

    .modules-list {
        flex: 1;
        overflow-y: scroll;
        padding: 1rem 2rem;

        &__item {
            display: block;
            margin-bottom: 1rem;
            cursor: move;
            transition: opacity .25s;

            &:hover {
                opacity: .8;
            }

            &:last-child {
                margin-bottom: 0;
            }
        }

        &__thumb {
            display: block;
            width: 100%;
            height: auto;
        }
    }

    .builder-module {
        transition: box-shadow .25s;
        cursor: move;

        &:hover {
            box-shadow: 0 0 0 .4rem #1fb6ff;
        }
    }

</style>



