import Vue from 'vue'
import VueRouter from 'vue-router'

import Builder from './components/Builder.vue'
import NotFound from './components/NotFound.vue'

Vue.use(VueRouter)

const routes = [
    {
        path: '/builder',
        component: Builder
    }, {
        path: '*',
        component: NotFound
    }
]

export default new VueRouter({
    mode: 'history',
    routes,
    linkActiveClass: '__active'
})


